<template>
    <div class="bg">
        <div class="online-box modelManagement">
            <div class="inp-line">
                <el-dropdown>
                    <el-button size="small">
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>1</el-dropdown-item>
                        <el-dropdown-item>2</el-dropdown-item>
                        <el-dropdown-item>3</el-dropdown-item>
                        <el-dropdown-item>4</el-dropdown-item>
                        <el-dropdown-item>5</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <el-input size="small" placeholder="" style="width: 300px"></el-input>
                <el-button size="small"></el-button>
                <el-button size="small"></el-button>
                <span class="float-btn">
                    <el-button size="small" type="primary"></el-button>
                    <el-button size="small"></el-button>
                    <el-button size="small"></el-button>
                </span>

            </div>
            <el-table :data="datalist" class="dataTable">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column label="ID">
                    <template slot-scope="scope">
                        <el-button type="text" size="small">{{scope.row.modelId}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="frameName" label=""></el-table-column>
                <el-table-column prop="formatName" label=""></el-table-column>
                <el-table-column prop="info" label=""></el-table-column>
                <el-table-column prop="time" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="editModel(scope.row.modelId)"></el-button>
                        <el-button type="text" size="small" @click="deleteModel"></el-button>
                        <el-button type="text" size="small" @click="dialogFormVisible2=true"></el-button>
                        <el-button type="text" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination-line">
                <el-pagination
                    @size-change="handleSizeChange"
                    background
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
                </el-pagination>
            </div>
            <!--  -->
            <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form :model="form" label-width="100px">
                    <el-form-item label="ID" >
                        <span style="color:#1777FF;">{{form.modelId}}</span>
                    </el-form-item>
                    <el-form-item label="ID" >
                        <el-input class="w300" size="small" v-model="form.newModelId" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input class="w300" size="small" v-model="form.modelDescribe" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="dialogFormVisible2">
                <el-form :model="derive" label-width="100px">
                    <el-form-item label="" >
                        <span style="color:#1777FF;">{{derive.url}}</span>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input class="w300" size="small" v-model="derive.newModelId" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input class="w300" size="small" v-model="derive.modelDescribe" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible2 = false"> </el-button>
                    <el-button type="primary" @click="dialogFormVisible2 = false"> </el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "home",
        filters: {
            modelStatusZn (val) {
                switch (val) {
                    case 0:
                        return ''
                    case 1:
                        return ''
                    case 2:
                        return ''
                    case 3:
                        return ''
                    case 4:
                        return ''
                }
            }
        },
        data () {
            return {
                dialogFormVisible: false, // 
                dialogFormVisible2: false, // 
                currentPage4: 4,
                form: {
                    modelId: '',
                    newModelId: '', // 
                    modelDescribe: '' // 
                },
                derive: {
                    url: 'https://www.baidu.com',
                    fileName: '',
                    saveUrl: ''
                },
                datalist: [
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: 'ID',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '',
                        time: '2020-08-10 12:30:00'
                    }
                ]
            }
        },
        methods: {
            editModel(modelId) {
                this.form.modelId = modelId
                this.dialogFormVisible = true
            },
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
            },
            deleteModel() {
                this.$confirm('xxx', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: $ { action } `
                        });
                    }
                });
            }
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .online-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        border-top-left-radius: 50px;
        padding: 30px 30px 20px;
        box-sizing: border-box;
        .w300{
            width: 300px;
        }
        .inp-line{
            padding: 0px 0 10px;
            .el-input{
                margin-right: 10px;
            }
            .el-button{
                width: 100px;
            }
            .float-btn{
                float: right;
            }
        }
        .pagination-line{
            background-color: #fff;
            padding: 20px 20px 10px;
            text-align: right;
        }
        .pagination-line{
            text-align: right;
        }
    }
</style>
<style>
    .dataTable .cell{
        font-size: 12px;
    }
    .modelManagement .el-dialog{
        width: 500px;
    }
</style>



